<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>翻译管理</el-breadcrumb-item>
      <el-breadcrumb-item>翻译列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!--    卡片视图-->
    <el-card>
      <!--      搜索与添加区域-->
      <el-row>
        <el-col :span="7">
          <el-input placeholder="请输入分类ID（1/2）" v-model="queryInfo.type" @clear="getTransformList" clearable>
            <el-button slot="append" icon="el-icon-search" @click="getTransformList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--      翻译列表区域-->
      <el-table :data="transformList" border stripe>
        <el-table-column label="ID" prop="id" align="center"></el-table-column>
        <el-table-column label="关键词" prop="cryWord" align="center"></el-table-column>
        <el-table-column label="关键词含义" prop="cryChinese" align="center"></el-table-column>
        <el-table-column label="翻译内容" prop="content" align="center"></el-table-column>
        <el-table-column label="建议" prop="cryMsg" align="center"></el-table-column>
        <el-table-column label="温馨提示" prop="cryTip" align="center"></el-table-column>
        <el-table-column label="类型" prop="type" align="center"></el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <!--            查看详情-->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--      分页区域-->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.current"
          :page-sizes="[2, 5, 10]"
          :page-size="queryInfo.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>
    <!--修改翻译的对话框-->
    <el-dialog
        title="修改翻译信息"
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
    >
      <el-form :model="editForm" ref="editFormRef" label-width="100px">
        <el-form-item label="关键词" prop="cryWord">
          <el-input v-model="editForm.cryWord"></el-input>
        </el-form-item>
        <el-form-item label="关键词含义" prop="cryChinese">
          <el-input v-model="editForm.cryChinese"></el-input>
        </el-form-item>
        <el-form-item label="翻译内容" prop="content">
          <el-input v-model="editForm.content"></el-input>
        </el-form-item>
        <el-form-item label="建议" prop="cryMsg">
          <el-input v-model="editForm.cryMsg"></el-input>
        </el-form-item>
        <el-form-item label="温馨提示" prop="cryTip">
          <el-input v-model="editForm.cryTip"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editTransformInfo">确 定</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Transform",
  data() {
    return {
      queryInfo: {
        id: null,
        current: 1,
        size: 5
      },
      total: 0,
      transformList: [],
      editDialogVisible: false,//显示与隐藏修改用户对话框
      editForm: {},//查询到的用户信息对象
    }
  },
  created() {
    this.getTransformList()
  },
  methods: {
    getTransformList() {
      this.$http.get('cryTranslateWords', {params: this.queryInfo}).then(res => {
        this.transformList = res.data.data.records
        this.total = res.data.data.total
      }).catch(err => err)
    },
    //监听 size 改变的事件
    handleSizeChange(newSize) {
      // console.log(newSize)
      this.queryInfo.size = newSize
      this.getTransformList()
    },
    //监听 页码值 改变的事件
    handleCurrentChange(newPage) {
      // console.log(newPage)
      this.queryInfo.current = newPage
      this.getTransformList()
    },
    showEditDialog(row){
      this.editForm = row
      this.editDialogVisible = true
    },
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    //修改分类信息并提交
    editTransformInfo(){
      this.$refs.editFormRef.validate(valid => {
        if (!valid) return
        this.$http.put('cryTranslateWords', this.editForm).then(res => {
          //关闭对话框
          this.editDialogVisible = false
          //刷新数据列表
          this.getTransformList()
          //提示修改成功
          this.$message.success('更新翻译信息成功')
        }).catch(err => {
          console.log(err)
          this.$message.error('更新翻译信息失败')
        })
      })
    }
  }
}
</script>

<style scoped>

</style>
